import React from 'react'
import {FormikContext, useFormik} from 'formik';
import { Button, Card, Nav } from 'react-bootstrap';
import { clearScrollMagic } from './helpers/scrollMagic';

const articleStyle = {
    margin: '3rem auto',
    padding: '0 0.5rem',
    maxWidth: '480px',
}

const validate = values => {
    let errors = {};
    if(!values.pswField) {
        errors.password = 'Field required';
    } 
    if (!values.emailField) {
        errors.email = 'Field required';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i
        .test(values.emailField)) {
        errors.email = 'Username should be an email';
    }
    return errors;
}

export const Account = () => {

    const clearHomeActive = () => {
        const home = document.getElementById('homeNavLink');
        home.classList.remove('active');
    }
    setTimeout(clearHomeActive, 50);

    clearScrollMagic();

    const formik = useFormik({
        initialValues: {
            emailField: '',
            pswField: ''
        },
        validate,
        onSubmit: values => {
            alert('Login Successful!');
            console.log('form:', values);
        }
    });

    const errorStyle = {
        color: 'red'
    }

    return (
        <section>
            <Card style={articleStyle}>
                <Card.Header>Featured Special Deal!</Card.Header>
                    <Card.Body>
                        <form onSubmit={formik.handleSubmit}>        

                            <div>Email</div>
                            <input 
                                id='emailField' 
                                type='email' 
                                onChange={formik.handleChange} 
                                onBlur={formik.handleBlur}
                                value={formik.values.emailField}
                            />
                            <div>
                            {formik.touched.emailField && formik.errors.email ? (
                                <span id='emailError' style={errorStyle}>{formik.errors.email}</span>
                                ) : null}

                            </div>
                            <div>Password</div>
                            <input 
                                id='pswField' 
                                type='password' 
                                onChange={formik.handleChange} 
                                onBlur={formik.handleBlur}
                                value={formik.values.pswField}
                            />
                            <div>
                            {formik.touched.pswField && formik.errors.password ? (
                                <span id='pswError' style={errorStyle}>{formik.errors.password}</span> 
                                ): null}<br/>

                            </div>
                            <Button type='submit' id='submitBtn'>Login</Button>
                            <Button type='button' id='createBtn'>Create Account</Button>

                        </form>
                    </Card.Body>
            </Card>
        </section>
    );
}