import React, { useEffect, useState } from 'react'
import { Navbar, Nav } from 'react-bootstrap';
import { Routes, Route, NavLink, useLocation } from "react-router-dom";
import { Header } from './components/Header';
import { Home } from './components/Home';
import { Deposit } from './components/Deposit';
import { Withdraw } from './components/Withdraw';
import { AllData } from './components/AllData';
import { Account } from './components/Account';
import { Footer } from './components/Footer';
import './styles/App.css';
import './styles/navbar.css';

const navTogglerStyle = {
    WebkitFilter: 'drop-shadow(1px 1px 1px white)',
    filter: 'drop-shadow(1px 1px 1px white)',
}

const spanStyle = {
    color: '#663333',
}

export const App = () => {
    const [expanded, setExpanded] = useState(false);

    const ScrollToTop = () => {
        const { pathname } = useLocation();
        useEffect(() => {
            window.scrollTo(0, 0);
        }, [pathname]);
        return null;
    }

    return (
        <div>

            <Navbar id='navbar'
                expanded={expanded}
                expand='md'
                className="navbar navbar-light sticky-top">
                <Navbar.Toggle 
                    className="navbar-toggler fw-bolder text-black"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarTogglerDemo02"
                    aria-controls="navbarTogglerDemo02"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                    onClick={() => setExpanded(expanded ? false : 'expanded')}
                    style={navTogglerStyle}
                >M<span style={spanStyle}>E</span>NU
                </Navbar.Toggle>
                <Navbar.Collapse className="collapse navbar-collapse" id="navbarTogglerDemo02">
                    <Nav className="navbar-nav me-auto mb-2 mb-lg-0" defaultActiveKey={1}>
                        <Nav.Link id='homeNavLink' eventKey={1} onClick={() => setExpanded(false)} href='/xchange/' as={NavLink} to='/xchange/'>Home</Nav.Link>
                        <Nav.Link eventKey={2} onClick={() => setExpanded(false)} href='/xchange/deposit' as={NavLink} to='/xchange/deposit'>Deposit</Nav.Link>
                        <Nav.Link eventKey={3} onClick={() => setExpanded(false)} href='/xchange/withdraw' as={NavLink} to='/xchange/withdraw'>Withdraw</Nav.Link>
                        <Nav.Link eventKey={4} onClick={() => setExpanded(false)} href='/xchange/alldata' as={NavLink} to='/xchange/alldata'>All Data</Nav.Link>
                        <Nav.Link eventKey={5} onClick={() => setExpanded(false)} href='/xchange/account' as={NavLink} to='/xchange/account'>Account</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>

            <Header />

            <ScrollToTop />

            <Routes>
                <Route path="/xchange/*" element={<Home />} />
                <Route path="/xchange/deposit" element={<Deposit />} />
                <Route path="/xchange/withdraw" element={<Withdraw />} />
                <Route path="/xchange/alldata" element={<AllData />} />
                <Route path="/xchange/account" element={<Account />} />
            </Routes>

            <Footer />

        </div>
    );
}